import React from 'react'
import Card from './Card'

let CardList = ({ robots }) => {
    return (
        <main>
            {
                robots.map((user, i) => {
                    return (
                        <Card 
                            key={i} 
                            id={robots[i].id} 
                            name={robots[i].name} 
                            email={robots[i].email} 
                        />
                    )
                })                   
            }
        </main>
    )
}

export default CardList
